<template>
  <div>
    <h2></h2>
    <div class="tt-row marginY-10">
      <p><span style="color: red">()</span></p>
      <div class="input"><el-input v-model="input"></el-input></div>
    </div>
    <div class="tt-row marginY-10">
      <p><span style="color: red">()</span></p>
      <div class="input"><el-input v-model="input"></el-input></div>
    </div>
    <div class="tt-row marginY-10">
      <p><span style="color: red">()</span></p>
      <div class="input"><el-input v-model="input"></el-input></div>
    </div>
    <div class="tt-row marginY-10 align-start">
      <p><span style="color: red">()</span></p>
      <div class="tt-row">
        <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar" />
          <img v-else src="../assets/fabu/uploadimg.png" alt="" />
        </el-upload>
        <p style="color: #6d707f; margin-left: 20px; font-size: 14px">
          <br />
          
        </p>
      </div>
    </div>
    <div class="tt-row marginY-10">
      <p><span style="color: red">()</span></p>
      <div class="input1"><el-input v-model="input"></el-input></div>
      <p>
        &nbsp;&nbsp;<span class="p2"></span>
      </p>
    </div>
    <div class="tt-row marginY-10">
      <p><span style="color: red">()</span></p>
      <div class="input1"><el-input v-model="input"></el-input></div>
      <p>
        &nbsp;&nbsp;<span class="p2"></span>
      </p>
    </div>
    <div class="tt-row marginY-10">
      <p><span style="color: red">()</span></p>
      <div class="input1"><el-input v-model="input"></el-input></div>
      <p>
        &nbsp;&nbsp;<span class="p2"></span>
      </p>
    </div>
    <div class="tt-row marginY-10">
      <p><span style="color: red">()</span></p>
      <div class="input1"><el-input v-model="input"></el-input></div>
      <p>
        &nbsp;&nbsp;<span class="p2"></span>
      </p>
    </div>
    <div class="tt-row marginY-10">
      <p></p>
      <el-radio v-model="radio" label="1"></el-radio>
      <el-radio v-model="radio" label="2"></el-radio>
    </div>
    <div class="tt-row marginY-10">
      <p></p>
      <el-radio v-model="radio" label="1"></el-radio>
      <el-radio v-model="radio" label="2"></el-radio>
    </div>
  </div>
</template>

<script>
export default {
  name: "publishtask2",
  data() {
    return {
      imgUrl: "",
    };
  },
  mounted(){
      this.$store.commit("cstep",2)
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
  },
};
</script>

<style scoped>
.p2 {
  font-size: 14px;
  color: #5f6372;
}
h2 {
  font-size: 22px;
  color: #333;
  padding: 10px 0 50px 0;
}
.input {
  width: 1000px;
}
.input2 {
  width: 230px;
}
.avatar-uploader {
  background: url(../assets/fabu/uploadimg.png);
  background-size: 100%;
  border-radius: 10px;
  overflow: hidden;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>



