            <i class="iconfont icon-chongzhi2"></i>
            <p></p>
          </li>
        </router-link>
        <router-link to="/funddetail">
          <li
            class="tt-row"
            :class="{ li: viewid == 13 }"
            @click="cview(13, '')"
          >
            <i class="iconfont icon-qianbao"></i>
            <p></p>
          </li>
        </router-link>
        <router-link to="/appealcenter">
          <li
            class="tt-row"
            :class="{ li: viewid == 14 }"
            @click="cview(14, '')"
          >
            <i class="iconfont icon-yichangshensu"></i>
            <p></p>
          </li>
        </router-link>
        <router-link to="/pricedetail">
          <li
            class="tt-row"
            :class="{ li: viewid == 15 }"
            @click="cview(15, '')"
          >
            <i class="iconfont icon-renminbicaiwuzhifu"></i>
            <p></p>
          </li>
        </router-link>
        <router-link to="/invitefriend">
