        <div class="navx between align-center">
          <p class="nav-title">{{ $store.state.navtitle }}</p>
          <div class="tt-row nav-menu">
            <p style="margin-right: 20px" class="uid">ID{{ uid }}</p>
            <span class="tt-row">
              <i class="iconfont icon-yonghu icon"></i>
              <p class="p1">{{ mobile }}</p>
            </span>
            <i class="bl"></i>
            <span class="tt-row" style="margin-right: 20px">
              <i class="iconfont icon-renminbicaiwuzhifu icon"></i>
              <span class="p1 tt-row"
                >
                <p style="color: red">{{ balance }}</p>
                &nbsp;</span
              >
              <p class="uid" @click="chongzhi()"></p>
            </span>
            <span class="tt-row">
              <i class="iconfont icon-account_pwd icon"></i>
              <p class="p1" @click="cpsdbox = true"></p>
            </span>
            <i class="bl"></i>
            <span class="tt-row" @click="sginOut()">
              <i class="iconfont icon-dianyuan icon"></i>
              <p class="p1"></p>
            </span>
          </div>
        </div>
      </div>
      <div class="tips tt-row">
        <el-button class="tips-btn center-xy" type="primary" plain
          ></el-button
        >
        <p>{{ tips }}</p>
      </div>
      <div class="main">
        <router-view></router-view>
      </div>
  <!--  -->
      <el-dialog
        :show-close="false"
        title=""
        :visible.sync="cpsdbox"
        width="600px"
        center
      >
        <div class="form-box center-xy tt-column">
          <div class="tt-row">
            <div class="btn" :class="{ this: cpsd == 1 }" @click="cpsd = 1">
              
            </div>
            <div class="btn" :class="{ this: cpsd == 2 }" @click="cpsd = 2">
              
            </div>
          </div>
          <div class="full" v-if="cpsd == 1">
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i></p>
              </div>
              <el-input
                type="password"
                placeholder=""
                v-model="oldpsd"
                clearable
              >
              </el-input>
            </div>
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i></p>
              </div>
              <el-input
                type="password"
                placeholder=""
                v-model="newpsd"
                clearable
              >
              </el-input>
            </div>
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i></p>
              </div>
              <el-input
                type="password"
                placeholder=""
                v-model="enternew"
                clearable
              >
              </el-input>
            </div>
          </div>
          <div v-if="cpsd == 2">
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i></p>
              </div>
              <el-input
                type="password"
                placeholder=""
                v-model="paypsd"
                clearable
              >
              </el-input>
            </div>
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i></p>
              </div>
              <el-input
                type="password"
                placeholder=""
                v-model="enterpay"
                clearable
