        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cpsdbox = false"> </el-button>
          <el-button type="primary" @click="cpsdbox = false"
            > </el-button
          >
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        :visible.sync="czbox"
        width="40%"
      >
        <div>
          <h2 style="text-indent:20px;">:</h2>
          <p style="font-size:20px;">
            <span style="color:red"></span> 
            <span style="color:red"></span>
            </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="czbox = false"
            ></el-button
          >
        </span>
      </el-dialog>
    </div>
