<script>
// import { mapMutations, mapState, mapGetters } from "vuex";
export default {
  name: "Menu",
  data() {
    return {
      viewid: 1,
      tips: "",
      uid: 19891331,
      mobile: 17560745114,
      balance: 3.1415926,

      oldpsd: null,
      newpsd: null,
      enternew: null,
      paypsd: null,
      enterpay: null,
      cpsdbox: false,
      cpsd: 1,
      czbox: false
    };
  },
  mounted() {
    // this.checkLogin();
  },
  computed: {},
  methods: {
    // ...mapMutations(['ctitle']),
    cview(a, title) {
      this.viewid = a;
      this.$store.commit("ctitle", title);
      this.$router.push({ path: "/withdrawPay" });
    },
    checkLogin() {
      if (!this.uid) {
        this.$notify({
          message: "",
        });
        this.$router.push({ path: "/login" });
      }
    },
    chongzhi() {
      this.czbox = true
      this.cview(12, "");
    },
    sginOut() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "!",
          });
          this.$router.push({ path: "/login" });
        })
        .catch(() => {});
    },
  },
};
</script>

<style scoped>
.bl {
  width: 2px;
  height: 14px;
  background: #f2f3f6;
  margin-right: 20px;
}
a {
  text-decoration: none;
}
.icon {
  border-radius: 50%;
  color: #c6c6c6;
  font-size: 24px;
  margin: 0 10px 0 0;
}
#menu {
  width: 100%;
  height: 100vh;
  background: #f0f1f5;
}
.left {
  min-width: 200px;
  height: 100vh;
  background: #353e50;
  padding-right: 20px;
  font-size: 16px;
  overflow-y: auto;
}
.left a {
  color: #808faf;
}
.left .iconfont {
  margin: 0 10px 0 0;
}
.left ul li {
  padding: 15px 0 15px 25px;
}
.left ul .li {
  background: #3672e8;
  color: #fff;
}
.right {
  flex-grow: 1;
  height: 100vh;
  padding: 0 20px;
}
.logo {
  height: 110px;
}
.logo img {
  width: 180px;
  height: 65px;
}
.nav {
  width: 100%;
  background: #fff;
  padding: 25px 0;
}
.navx {
  height: 30px;
}
.nav .p1 {
  font-size: 16px;
  color: #868686;
  margin-right: 20px;
  font-weight: bold;
}
.nav-title {
  margin-left: 40px;
  color: #5f6737;
  font-size: 18px;
  font-weight: bold;
}
.nav-menu {
  margin: 0 20px 0 0;
}
.uid {
  font-size: 20px;
  color: #0098e1;
  font-weight: 700;
}
.tips {
  /* height: 68px; */
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 18px;
  text-indent: 10px;
  padding: 20px 0;
}
.tips-btn {
  width: 56px;
  height: 28px;
  font-size: 12px;
}
.main {
  width: 100%;
  flex-grow: 1;
  overflow-x: hidden;
}
/*  */
.form-box {
  width: 370px;
  margin: 0 auto;
}
.form-box .btn {
  padding: 10px 15px;
  background: #f0f1f5;
  border-radius: 5px;
  margin: 10px;
  transition: all 0.5s;
}
.form-box .this {
  color: #fff !important;
  background: #3672e8;
}
.form-box .btn:hover {
  color: #000000;
  background: #5ae1f7;
  transition: all 0.5s;
}
.form-row-left {
  width: 150px;
  color: #606266;
}
</style>



